const idiom = (key) => (...idioms) => {
    return {
        [key]: [...idioms],
    };
};
export const waitFor = idiom('waitFor');
export const block = idiom('block');
export const request = (...idioms) => {
    return {
        request: [...idioms],
    };
};
export const delegate = (...gens) => function* () {
    for (const gen of gens) {
        yield* gen();
    }
};
export const loop = (gen, callback = () => true) => function* () {
    while (callback()) {
        yield* gen();
    }
};
export const strand = (...idiomSets) => function* () {
    for (const set of idiomSets) {
        yield set;
    }
};
